<template>
  <div id="index">
    <router-link tag="div" :to="{path: '/one'}" class="green"></router-link>
    <router-link tag="div" :to="{path: '/two'}" class="delete"></router-link>
    <router-link tag="div" :to="{path: '/three'}" class="green"></router-link>
    <router-link tag="div" :to="{path: '/four'}" class="pink"></router-link>
    <router-link tag="div" :to="{path: '/five'}" class="green"></router-link>
    <router-link tag="div" :to="{path: '/six'}" class="green"></router-link>
    <router-link tag="div" :to="{path: '/seven'}" class="green"></router-link>
    <!-- <div id="test">
      <form id="upload-form" action="/upload" enctype="multipart/form-data" method="post">
        <input id="selece-files" type="file" name="fileToUpload" />
        <input id="do-upload" type="submit" value="" />
      </form>
    </div> -->
  </div>  
</template>

<script>
export default {
  methods: {
    submit() {
      var Upload = (function(win, upload) {
        upload.submit = function() {
          var self = this,
            form = document.getElementById("form"),
            formdata = new FormData(form),
            xhr = new XMLHttpRequest();

          // xhr.upload  iOS Safari  Android 4.0+ Chrome 
          xhr.upload.addEventListener("progress", self.onProgress, false);
          xhr.addEventListener("load", self.onSuccess, false);
          xhr.addEventListener("error", self.onError, false);
          xhr.addEventListener("abort", self.onCancel, false);

          xhr.open("post", form.action, false);
          xhr.send(formdata);
        };

        //  onProgress 
        upload.onProgress = function(e) {
          if (e.lengthComputable) {
            var progress = Math.round(e.loaded * 100 / e.total) + "%";
            console.log("on progress: ", progress);
          }
        };

        upload.onError = function() {};
        upload.onCancel = function() {};

        // 
        upload.onSuccess = function() {};

        return upload;
      })(window, window.Upload || {});
    }
  }
};
</script>

<style lang="scss">
#index {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: center;
  background: #fff;
  div {
    line-height: 60px;
    font-size: 30px;
  }
  .pink {
    color: pink;
  }
  // OK
  .green{
    color: lightgreen;
  }
  .delete {
    text-decoration: line-through;
    color: #eee;
  }
  #test {
    font-size: 14px;
    width: 100%;
  }
}
</style>



